export default {
	name: "foot",
	props: {
		"btm": {
			type: Boolean,
			default: true
		},
		"color": {
			type: Boolean,
			default: false
		},
		"position": {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			codeList: [],
			footInfo: {}
		}
	},
	created() {
		this.getFootInfo();
		this.getFootConfig();
	},
	methods: {
		getFootInfo() {
			this.$http.getFootInfo({}, res => {
				if (res.code == 1) {
					this.codeList = res.data;
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
		},
		getFootConfig() {
			this.$http.getFootConfig({}, res => {
				if (res.code == 1) {
					this.footInfo = res.data;
				} else {
					this.$common.errorToShow(res.msg)
				}
			})
		}
	}
}
